@use "base/variables" as v;
@use "base/mixins" as m;

.modal {
    position: absolute;
    background-color: rgb(0 0 0 / 0.8);
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transition: all 0.8s ease;
        top: 0;
        opacity: 0;

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }

        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }

        legend {
            display: block;
            font-size: 2rem;
            margin-bottom: 2rem;
            font-weight: bold;
            text-align: center;
        }

        .campo {
            input {
                color: v.$negro;
                background-color: v.$blanco;

                &:focus {
                    background-color: darken(v.$azulOscuro, 10%);
                    border-color: v.$blanco;
                    color: v.$blanco;
                }

                &::placeholder {
                    color: v.$negro;
                }
            }
        }

        .opciones {
            
            @include m.tablet{
                display: flex;
                justify-content: space-between;
                gap: 2rem;
            }

            .submit-nueva-tarea {
                @include m.boton(lighten(v.$naranja, 5%), v.$blanco);
                background-color: v.$naranja;
                margin-bottom: 2rem;

                @include m.tablet{
                    margin-bottom: 0;
                }
            }

            .cerrar-modal {
                @include m.boton(darken(v.$azulOscuro, 20%), v.$blanco);
                background-color: v.$azulOscuro;
            }
        }
    }
}
