@use "base/variables" as v;
@use "base/mixins" as m;

.mensaje-background {
    background-color: v.$naranja;

    .mensaje {
        display: flex;
        flex-direction: column;
        min-height: 100vh;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            align-items: center;
        }

        .izquierda {
            text-align: center;
            .uptask {
                @include m.textoDegradado(v.$azulOscuro);
            }

            .nav {
                .boton {
                    @include m.boton(v.$azuClaro, v.$blanco);
                    width: 60%;
                }
            }
        }

        .derecha {
            padding: 2rem;

            p {
                font-weight: bold;
                color: v.$negro;
            }
        }
    }
}
