@use "base/variables" as v;
@use "base/mixins" as m;

.global {
    background-color: v.$blanco;
    .uptask {
        font-weight: 900;
        padding: 4rem 0;
        h1 {
            text-align: center;
            color: v.$negro;
        }

        .banner {
            display: flex;
            flex-direction: column-reverse;

            @include m.tablet {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                align-items: center;
                gap: 2rem;
            }

            .contenido {
                padding: 0 2rem;
                p {
                    color: v.$gris;
                }

                .nav {
                    .boton {
                        @include m.boton(v.$naranja, v.$negro);
                        width: 60%;
                        margin: 0;
                        padding: 1.4rem 0;

                        @include m.telefono {
                            width: 60%;
                        }

                        @include m.tablet {
                            width: 40%;
                        }
                    }
                }
            }
        }

        .lenguajes {
            h3 {
                text-align: center;
                font-size: 3rem;
            }

            .imagenes {
                text-align: center;

                @include m.tablet {
                    display: grid;
                    grid-template-columns: repeat(5, 1fr);
                    gap: 2rem;
                }

                .imagen {
                    width: 50%;
                }
            }
        }
    }
}
.footer {
    background-color: v.$naranja;

    .contenedor {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        padding: 3rem 0;

        @include m.tablet {
            flex-direction: row;
        }

        .social {
            h4 {
                text-align: center;
                font-weight: 400;
                color: v.$negro;
                margin-top: 0;
                letter-spacing: 0.2rem;
            }
            i {
                font-size: 2.5rem;
                cursor: pointer;
                color: v.$negro;
                transition: all 0.5s ease;

                &:hover {
                    transform: scale(1.5);
                    margin: 0 1rem;
                    color: v.$blanco;
                }
            }
        }

        p {
            font-size: 4.5rem;
            font-weight: 600;
            color: v.$blanco;
            margin: 0;

            span {
                font-size: 5rem;
                color: v.$negro;
            }
        }
    }
}
