@use "base/variables" as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin boton($colorBorde, $colorTexto) {
    border: .1rem solid $colorBorde;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    transition-property: background-color;
    transition-duration: 0.3s;
    text-align: center;
    display: block;
    width: 100%;
    border-radius: 0.5rem;
    margin: 0 auto;
    font-family: "Arvo", sans-serif;
    &:hover {
        background-color: $colorBorde;
        cursor: pointer;
        color: v.$blanco;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin textoDegradado($color) {
    background: linear-gradient(to right, $color 20%, darken($color, 30%) 100%);
    color: transparent;
    background-clip: text;
    font-size: 6rem;
    margin: 0;
    padding: 0;

    @include tablet {
        font-size: 10rem;
    }
}
