@use "base/variables" as v;
@use "base/mixins" as m;

.no-proyectos {
    color: v.$blanco;
    font-weight: bold;

    a {
        font-size: 2.2rem;
        color: v.$negro;
        border-bottom: 0.2rem solid v.$naranja;
        cursor: pointer;
        transition: all 0.5s ease;

        &:hover {
            color: v.$blanco;
            border-color: v.$negro;
        }
    }
}

.listado-proyectos {
    list-style: none;
    padding: 0;

    .proyecto {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: v.$blanco;
        font-weight: bold;
        border: 0.1rem solid v.$azulOscuro;
        min-height: 12rem;
        border-radius: 0.5rem;
        line-height: 2rem;
        transition: all 0.5s ease;

        &:hover {
            border: none;
            background-color: darken(v.$azulOscuro, 20%);
            transform: translateY(0.2rem);
        }
    
        .proyecto-titulo{
            color: v.$blanco;
            width: 100%;
            min-height: 9rem;
            padding-top: 3rem;
        }

        .acciones{
            margin: 0;
            width: 100%;
            min-height: 3rem;
            display: flex;
            flex-direction: row;
            

            a{
                padding-top: .5rem;
                flex: 1;
                border-radius: .5rem;
                text-align: center;
            }

            .eliminar{
                border-top: .1rem solid v.$naranja;
                border-left: .1rem solid v.$naranja;
                transition: all .5s ease;

                &:hover{
                    background-color: v.$naranja;
                }
            }

            .actualizar{
                border-top: .1rem solid green;
                border-right: .1rem solid green;

                &:hover{
                    background-color: green;
                }
            }
        }
    
    } 
    @include m.grid(1, 1.5rem);

    @include m.tablet {
        @include m.grid(2, 1.5rem);
    }

    @include m.desktop {
        @include m.grid(4, 1.5rem);
    }

   
}
