@use "base/variables" as v;
@use "base/mixins" as m;

.crear-background {
    background-color: v.$azulOscuro;

    .crear {
        display: flex;
        flex-direction: column;
        min-height: 100vh;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            align-items: center;
        }

        .izquierda {
            text-align: center;
            .uptask {
                @include m.textoDegradado(v.$naranja);
            }

            .nav {
                .boton {
                    @include m.boton(v.$naranja, v.$blanco);
                    width: 60%;
                }
            }
        }

        .derecha {
            padding: 2rem;

            .formulario {
                .campo {
                    input:not([type="submit"]) {
                        flex: 1;
                        border: none;
                        outline: none;
                        padding: 1rem;
                        border-bottom: 0.2rem solid v.$naranja;
                        background-color: transparent;
                        color: v.$blanco;
                        transition: all 0.3s ease-in;

                        &:focus {
                            background-color: darken(v.$azulOscuro, 10%);
                            border-color: v.$blanco;
                        }

                        &::placeholder {
                            color: v.$blanco;
                        }
                    }
                }

                .boton {
                    @include m.boton(v.$naranja, v.$blanco);
                    background-color: transparent;
                    width: 90%;
                    border-radius: 0;

                    @include m.telefono {
                        width: 50%;
                    }
                }
            }

            .acciones {
                a {
                    color: v.$naranja;
                }
            }
        }
    }
}
