@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
    margin-top: 5rem;
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        gap: 2rem;

        label {
            display: none;

            @include m.tablet {
                display: block;
                flex: 0 0 10rem;
                color: v.$negro;
                font-weight: bold;
            }
        }

        input:not([type="submit"]) {
            flex: 1;
            border: none;
            outline: none;
            padding: 1rem;
            border-bottom: 0.2rem solid v.$azulOscuro;
            background-color: transparent;
            color: v.$blanco;
            transition: all 0.3s ease-in;

            &:focus {
                background-color: darken(v.$naranja, 10%);
                border-color: v.$blanco;
            }

            &::placeholder {
                color: v.$blanco;
            }
        }
    }
}
