@use "base/variables" as v;
@use "base/mixins" as m;

.darkMode {
    background-color: lighten(v.$negro, 10%);

    .sidebar {
        background-color: lighten(v.$negro, 10%);
        box-shadow: 0.1rem 0 0.9rem 0 rgba(0, 0, 0, 0.4);

        .sidebar-nav {
            .link {
                color: v.$blanco;
                &:hover,
                &.activo {
                    background-color: darken(v.$azuClaro, 5%);
                    border-radius: 0.5rem;
                }

                a {
                    color: v.$blanco;
                }
            }
        }
    }

    .principal {
        .barra {
            background-color: lighten(v.$negro, 10%);

            p {
                color: v.$azuClaro;
            }
        }

        .barra-mobile {
            background-color: lighten(v.$negro, 10%);
        }

        .contenido {
            .nombre-pagina {
                color: v.$naranja;
            }

            label {
                color: v.$azuClaro;
            }

            input:not([type="submit"]) {
                background-color: transparent;

                &::placeholder {
                    color: v.$blanco;
                }
            }

            .filtros {
                background-color: transparent;
                h2 {
                    color: v.$naranja;
                }
            }
        }
    }

    .modal {
        .formulario {
            background-color: lighten(v.$negro, 10%);

            legend {
                color: v.$naranja;
            }

            label {
                color: v.$azuClaro;
            }

            input {
                background-color: transparent;

                &::placeholder {
                    color: v.$blanco;
                }

                &:focus {
                    background-color: transparent;
                    border-color: v.$naranja;
                }
            }
        }
    }
}
