@use "base/variables" as v;
@use "base/mixins" as m;

.login-background {
    background-color: v.$naranja;

    .login {
        display: flex;
        flex-direction: column;
        min-height: 100vh;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            align-items: center;
        }

        .izquierda {
            text-align: center;
            position: relative;
            .uptask {
                @include m.textoDegradado(v.$azulOscuro);
            }

            .nav {
                .boton {
                    @include m.boton(v.$azuClaro, v.$negro);
                    width: 60%;
                }
            }
        }

        .derecha {
            padding: 2rem;

            .formulario {
                .boton {
                    @include m.boton(v.$azulOscuro, v.$negro);
                    background-color: transparent;
                    width: 90%;
                    border-radius: 0;

                    @include m.telefono {
                        width: 50%;
                    }
                }
            }

            .acciones {
                a {
                    color: v.$azulOscuro;
                    transition: color 0.5s ease;

                    &:hover {
                        color: v.$blanco;
                    }
                }
            }
        }
    }
}
