::-webkit-scrollbar {
  width: 1.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: #1b1e23;
  border: 1rem;
  transition: all 0.3s ease-in;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #e13b2b;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 80%;
  max-width: 140rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}
h1::selection,
h2::selection,
h3::selection,
h4::selection {
  color: #326789;
  background-color: #89a4b9;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #848588;
  line-height: 1.8;
  font-weight: 200;
}

a::selection, p::selection, label::selection, input::selection, span::selection {
  color: #326789;
  background-color: #89a4b9;
}

.tagline {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  color: #1b1e23;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 2rem;
  font-family: "Poppins", sans-serif;
  color: #1b1e23;
  line-height: 3rem;
}

/** Media Queries **/
.global {
  background-color: #e9eef2;
}
.global .uptask {
  font-weight: 900;
  padding: 4rem 0;
}
.global .uptask h1 {
  text-align: center;
  color: #1b1e23;
}
.global .uptask .banner {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .global .uptask .banner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 2rem;
  }
}
.global .uptask .banner .contenido {
  padding: 0 2rem;
}
.global .uptask .banner .contenido p {
  color: #848588;
}
.global .uptask .banner .contenido .nav .boton {
  border: 0.1rem solid #e65c4f;
  padding: 1.5rem 4rem;
  color: #1b1e23;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  margin: 0 auto;
  font-family: "Arvo", sans-serif;
  width: 60%;
  margin: 0;
  padding: 1.4rem 0;
}
.global .uptask .banner .contenido .nav .boton:hover {
  background-color: #e65c4f;
  cursor: pointer;
  color: #e9eef2;
}
@media (min-width: 480px) {
  .global .uptask .banner .contenido .nav .boton {
    width: 60%;
  }
}
@media (min-width: 768px) {
  .global .uptask .banner .contenido .nav .boton {
    width: 40%;
  }
}
.global .uptask .lenguajes h3 {
  text-align: center;
  font-size: 3rem;
}
.global .uptask .lenguajes .imagenes {
  text-align: center;
}
@media (min-width: 768px) {
  .global .uptask .lenguajes .imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 2rem;
  }
}
.global .uptask .lenguajes .imagenes .imagen {
  width: 50%;
}

.footer {
  background-color: #e65c4f;
}
.footer .contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer .contenedor {
    flex-direction: row;
  }
}
.footer .contenedor .social h4 {
  text-align: center;
  font-weight: 400;
  color: #1b1e23;
  margin-top: 0;
  letter-spacing: 0.2rem;
}
.footer .contenedor .social i {
  font-size: 2.5rem;
  cursor: pointer;
  color: #1b1e23;
  transition: all 0.5s ease;
}
.footer .contenedor .social i:hover {
  transform: scale(1.5);
  margin: 0 1rem;
  color: #e9eef2;
}
.footer .contenedor p {
  font-size: 4.5rem;
  font-weight: 600;
  color: #e9eef2;
  margin: 0;
}
.footer .contenedor p span {
  font-size: 5rem;
  color: #1b1e23;
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  gap: 2rem;
}
.formulario .campo label {
  display: none;
}
@media (min-width: 768px) {
  .formulario .campo label {
    display: block;
    flex: 0 0 10rem;
    color: #1b1e23;
    font-weight: bold;
  }
}
.formulario .campo input:not([type=submit]) {
  flex: 1;
  border: none;
  outline: none;
  padding: 1rem;
  border-bottom: 0.2rem solid #326789;
  background-color: transparent;
  color: #e9eef2;
  transition: all 0.3s ease-in;
}
.formulario .campo input:not([type=submit]):focus {
  background-color: #e03322;
  border-color: #e9eef2;
}
.formulario .campo input:not([type=submit])::placeholder {
  color: #e9eef2;
}

.acciones {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .acciones {
    text-align: justify;
  }
}
.acciones a {
  color: #e9eef2;
}

.alerta {
  color: #1b1e23;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  margin: 1rem 0;
  max-width: 80rem;
  font-size: 1.4rem;
}
.alerta.error {
  background-color: #ec857c;
  border-bottom: 0.2rem solid #e9eef2;
}
.alerta.exito {
  background-color: #313740;
  border-bottom: 0.2rem solid #e65c4f;
  color: #e9eef2;
}

