  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  margin: 0 auto;
  font-family: "Arvo", sans-serif;
  background-color: #326789;
}
.modal .formulario .opciones .cerrar-modal:hover {
  background-color: #172f3e;
  cursor: pointer;
  color: #e9eef2;
}

.filtros {
  margin: 2rem 0;
  padding: 2rem 0;
  border: 0.1rem solid #78a6c8;
  border-radius: 0.5rem;
  background-color: #8ab2cf;
}
@media (min-width: 768px) {
  .filtros {
    display: grid;
    grid-template-areas: "filtros categorias categorias";
    align-items: center;
  }
}
.filtros h2 {
  font-size: 2rem;
  grid-area: filtros;
  margin-bottom: 1rem;
  color: #1b1e23;
}
@media (min-width: 768px) {
  .filtros h2 {
    margin-bottom: 0;
  }
}
.filtros .filtros-inputs {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    grid-area: categorias;
    justify-content: space-around;
    flex-direction: row;
  }
}
.filtros .filtros-inputs .campo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.filtros .filtros-inputs .campo label {
  color: #1b1e23;
  flex: 1;
  text-align: justify;
  cursor: pointer;
}
.filtros .filtros-inputs .campo input[type=radio] {
  cursor: pointer;
  accent-color: #e65c4f;
  width: 2rem;
  height: 2rem;
}

