@use "variables" as v;

h1,
h2,
h3,
h4 {
    font-family: v.$fuente_headings;
    margin: 0 0 calc(v.$separacion / 2) 0;
    font-weight: 900;

    &::selection{
        color: v.$azulOscuro;
        background-color: darken(v.$blanco, 30%);
    }
}
h1 {
    font-size: 6rem;
}
h2 {
    font-size: 5rem;
}
h3 {
    font-size: 4rem;
}
p {
    font-size: 2rem;
    color: v.$gris;
    line-height: 1.8;
    font-weight: 200;
}

a, p, label, input, span{
    &::selection{
        color: v.$azulOscuro;
        background-color: darken(v.$blanco, 30%);
    }
}

.tagline {
    font-family: v.$fuente_headings;
    font-weight: 900;
    color: v.$negro;
    text-align: center;
}

.descripcion-pagina {
    text-align: center;
    font-size: 3rem;
    font-weight: bold;
    margin: 0;
    margin-bottom: 2rem;
    font-family: v.$fuente_headings;
    color: v.$negro;
    line-height: 3rem;
}
