@use "base/variables" as v;
@use "base/mixins" as m;

.alerta {
    color: v.$negro;
    padding: 1rem;
    text-transform: uppercase;
    font-weight: bold;
    margin: 1rem 0;
    max-width: 80rem;
    font-size: 1.4rem;
    
    &.error{
        background-color: lighten(v.$naranja,10%);
        border-bottom: .2rem solid v.$blanco;
    }
    &.exito{
        background-color: lighten(v.$negro, 10%);
        border-bottom: .2rem solid v.$naranja;
        color: v.$blanco;
    }
}
