@use "base/variables" as v;
@use "base/mixins" as m;

.confirmar-background {
    background-color: v.$blanco;

    .confirmar {
        display: flex;
        flex-direction: column;
        min-height: 100vh;

        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 2fr;
            align-items: center;
        }

        .izquierda {
            text-align: center;
            .uptask {
                @include m.textoDegradado(v.$azulOscuro);
            }

            .nav {
                .boton {
                    @include m.boton(v.$azuClaro, v.$blanco);
                    width: 60%;
                }
            }
        }

        .derecha {
            padding: 2rem;

            .acciones {
                text-align: center;
                a {
                    @include m.boton(v.$negro, v.$blanco);
                    width: fit-content;
                }
            }
        }
    }
}
